import DataView from "./DataView";

import { formatWeatherString } from "../helper/metar-ui-helper";

import { Box } from "@mui/material";

type Props = {
  props: {
    altim: number | null;
    slp: number;
    temp: number;
    dewp: number;
    tempUnitToggle: Function;
    tempUnit: string;
    wxString: string;
    visibilityMeters: number;
  };
};

function DataPanel({ props }: Props) {
  return (
    <Box className="weather-data">
      {props.slp !== null && props.altim === null && (
        <DataView
          data={[
            {
              description: "SLP",
              value: Math.round(props.slp),
            },
          ]}
          unit={"hPa"}
        ></DataView>
      )}
      {props.altim !== null && props.slp === null && (
        <DataView
          data={[
            {
              description: "QNH",
              value: Math.round(props.altim),
            },
          ]}
          unit={"hPa"}
        ></DataView>
      )}
      {props.slp !== null && props.altim !== null && (
        <DataView
          data={[
            {
              description: "QNH",
              value: Math.round(props.altim),
            },
            {
              description: "SLP",
              value: Math.round(props.slp),
            },
          ]}
          unit={"hPa"}
        ></DataView>
      )}
      <DataView
        data={[
          {
            description: "Visibility",
            value: props.visibilityMeters,
          },
        ]}
        unit={"m"}
      ></DataView>
      {props.wxString && (
        <DataView
          data={[
            {
              description: "Precipitation",
              value: formatWeatherString(props.wxString),
            },
          ]}
        ></DataView>
      )}
      {props.tempUnit === "C" ? (
        <>
          <DataView
            data={[
              {
                description: "Temperature",
                value: props.temp,
              },
              {
                description: "Dewpoint",
                value: props.dewp,
              },
            ]}
            unit={props.tempUnit}
            tempUnitToggle={() => props.tempUnitToggle(props.tempUnit)}
          ></DataView>
        </>
      ) : (
        <>
          <DataView
            data={[
              {
                description: "Temperature",
                value: Math.round(props.temp * (9 / 5) + 32),
              },
              {
                description: "Dewpoint",
                value: Math.round(props.dewp * (9 / 5) + 32),
              },
            ]}
            unit={props.tempUnit}
            tempUnitToggle={() => props.tempUnitToggle(props.tempUnit)}
          ></DataView>
        </>
      )}
    </Box>
  );
}

export default DataPanel;
